@use "./functions" as *;

@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Arabic:wght@100;200;300;400;500;600;700&display=swap");

$min-fs: 10;
$max-fs: 160;

// font weights
$font-weights: (
  300: 300,
  400: 400,
  500: 500,
  600: 600,
  700: 700,
  800: 800,
  900: 900,
);

// Initialize the font sizes map
$font-sizes: () !default;

@for $i from $min-fs through $max-fs {
  // Only add font sizes for 12, 14, 16, etc.
  @if $i % 2 == 0 {
    // Check if the number is even (12, 14, 16, etc.)
    $font-sizes: map-merge(
      $font-sizes,
      (
        $i: rem($i),
      )
    );
  }
}

// defaults typography
*,
*::before,
*::after {
  box-sizing: border-box;
}

*:not(i) {
  font-family: "IBM Plex Sans Arabic", sans-serif;
  text-align: start;
}

body {
  margin: 0;
  padding: 0;
  width: 100vw;
  overflow-x: hidden;
}
