/* !!!: to set how many rows to show in element before clamp */
.outer-container {
  width: 100%;
  padding-right: 25px;
  padding-left: 25px;
  margin-right: auto;
  margin-left: auto;
}
@media (width < 48em) {
  .outer-container {
    padding-left: 10px;
    padding-right: 10px;
  }
}
@media (min-width: 768px) {
  .outer-container {
    max-width: 95%;
  }
}
@media (min-width: 992px) {
  .outer-container {
    max-width: 92%;
  }
}
@media (min-width: 1200px) {
  .outer-container {
    max-width: 90%;
  }
}
@media all and (min-width: 1920px) {
  .outer-container {
    max-width: 1700px;
  }
}
@media all and (min-width: 3400px) {
  .outer-container {
    max-width: 2000px;
  }
}

.control-bar {
  padding-block: 2px;
}
.control-bar .control-btn {
  width: 35px;
  height: 35px;
  border: none !important;
  border-radius: 50% !important;
  display: flex;
  align-items: center;
  justify-content: center;
}
.control-bar .control-btn:hover {
  background-color: #d2d6db;
}
.control-bar .control-btn .icon {
  width: 60%;
  height: 60%;
}

/* !!!: to set how many rows to show in element before clamp */
.navbar {
  transition: all 0.3s ease;
}
.navbar .navbar-brand {
  margin: 0;
  padding: 0.5rem 0;
}
.navbar .navbar-brand img {
  transition: height 0.3s ease;
  height: 50px;
}
@media (width < 48em) {
  .navbar .navbar-brand img {
    height: 40px !important;
  }
}
@media (width < 62em) {
  .navbar .navbar-brand img {
    height: 45px !important;
  }
}
@media (width < 36em) {
  .navbar .navbar-brand img {
    height: 35px !important;
  }
}
.navbar .mega-menu {
  box-shadow: 0 4px 6px -4px rgba(0, 0, 0, 0.1);
  border: none;
  border-radius: 0 0 0.5rem 0.5rem;
  padding: 1.5rem 0;
  margin-top: 0;
}
.navbar .navbar-nav {
  height: 60px;
  gap: 0.25rem;
}
.navbar .navbar-nav .nav-link {
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #161616;
  font-weight: 500;
  padding: 0 1.25rem;
  transition: all 0.2s ease;
  border-radius: 0.25rem;
}
.navbar .navbar-nav .nav-link.active {
  font-weight: 600;
  color: #1b8354 !important;
}
.navbar .navbar-nav .nav-link.active::after {
  background-color: #1b8354 !important;
  width: 100%;
}
.navbar .navbar-nav .nav-link:hover {
  background-color: rgba(243, 244, 246, 0.5);
  color: #161616;
}
.navbar .navbar-nav .nav-link:hover::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  height: 3px;
  width: 70%;
  background-color: #9da4ae;
  border-radius: 3px;
  transition: all 0.3s ease;
}
.navbar .navbar-nav .nav-link.dropdown-toggle::after {
  display: none;
}
@media (width < 62em) {
  .navbar .navbar-nav {
    height: auto;
    flex-direction: column;
    gap: 0.5rem;
    padding: 1rem 0;
  }
  .navbar .navbar-nav .nav-link {
    height: auto;
    padding: 0.75rem 1.5rem;
    justify-content: flex-start;
  }
  .navbar .navbar-nav .nav-link:hover::after {
    display: none;
  }
}
@media (width < 62em) {
  .navbar .navbar-nav {
    height: auto;
    flex-direction: column;
    gap: 0.5rem;
    padding: 1rem 0;
  }
  .navbar .navbar-nav .nav-item {
    width: 100%;
  }
  .navbar .navbar-nav .nav-item .nav-link {
    height: auto;
    padding: 0.75rem 1.5rem;
    justify-content: flex-start;
    border-radius: 0.25rem;
  }
  .navbar .navbar-nav .nav-item .nav-link:hover {
    background-color: rgba(243, 244, 246, 0.5);
  }
  .navbar .navbar-nav .nav-item .nav-link:hover::after {
    display: none;
  }
  .navbar .navbar-nav .nav-item.dropdown .dropdown-toggle::after {
    display: inline-block;
    margin-right: auto;
    margin-left: 0.5rem;
  }
  .navbar .navbar-nav .nav-item.dropdown .dropdown-menu {
    position: static !important;
    transform: none !important;
    border: none;
    box-shadow: none;
    padding: 0;
  }
  .navbar .navbar-nav .nav-item.dropdown .dropdown-menu .dropdown-item {
    padding: 0.5rem 2rem;
  }
  .navbar .navbar-nav .nav-item.dropdown .dropdown-menu .dropdown-item:focus {
    color: #161616;
  }
  .navbar .d-flex.navbar-nav {
    flex-direction: row !important;
    justify-content: flex-start;
    padding: 1rem 0;
    margin-top: 0.5rem;
    border-top: 1px solid #e5e7eb;
  }
  .navbar .d-flex.navbar-nav .btn,
  .navbar .d-flex.navbar-nav .dropdown {
    margin: 0 0.5rem 0 0;
  }
}
@media (max-width: 1024px) and (min-width: 992px) {
  .navbar .navbar-nav .nav-link {
    padding: 0 0.75rem;
    font-size: 0.9rem;
  }
}

@media (width < 62em) {
  .offcanvas-nav {
    width: 320px !important;
  }
  .offcanvas-nav .offcanvas-body .navbar-nav .dropdown-menu li a.dropdown-item {
    padding-left: 2.5rem;
  }
}

.dropdown-menu-no-transform {
