/* -------------------------------------------------------------------------- */
.form-label {
  text-transform: capitalize;
  font-family: "IBM Plex Sans Arabic", sans-serif;
  font-weight: 600;
}
.form-label span {
  margin-inline: 4px;
  color: red;
}

