/*                                form control                                */
/* -------------------------------------------------------------------------- */
.form-control:focus {
  box-shadow: none !important;
}

/* -------------------------------------------------------------------------- */
/*                                 form group                                 */
/* -------------------------------------------------------------------------- */
.input-group {
  overflow: hidden;
  border: 1px solid #9da4ae;
  border-radius: 6px;
}
.input-group:has(.form-control:focus) {
  border-color: #9da4ae;
}
.input-group:has(.form-control.is-invalid) {
  border-color: #dc3545;
}
.input-group .input-group-text {
  width: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 0;
  padding: 0;
  background-color: transparent;
}
.input-group .form-select {
  border: none;
}
.input-group .form-control {
  border-radius: 0;
  border: none;
  border-color: transparent;
  box-shadow: none;
}
.input-group .form-control.is-invalid, .input-group .form-control.is-valid {
  box-shadow: none;
}

.form-select {
  background-position: right 0.75rem center;
  padding: 0.375rem 2.25rem 0.375rem 0.75rem;
}
html[dir=rtl] .form-select {
  background-position: left 0.75rem center;
  padding: 0.375rem 0.75rem 0.375rem 2.25rem;
}

/* -------------------------------------------------------------------------- */
/*                                 validation                                 */
/* -------------------------------------------------------------------------- */
.is-invalid.form-control {
  padding-inline: 1rem !important;
  padding-inline-end: calc(1.5em + 0.75rem) !important;
}
html[dir=rtl] .is-invalid.form-control {
  background-position: left calc(0.375em + 0.1875rem) center;
}
html[dir=ltr] .is-invalid.form-control {
  background-position: right calc(0.375em + 0.1875rem) center;
}
.is-invalid.form-control {
  padding-inline: 1rem !important;
  padding-inline-end: calc(1.5em + 0.75rem) !important;
}
html[dir=rtl] .is-invalid.form-control {
  background-position: left calc(0.375em + 0.1875rem) center;
}
html[dir=ltr] .is-invalid.form-control {
  background-position: right calc(0.375em + 0.1875rem) center;
}
.is-invalid.form-select {
  padding-inline: 0.8rem !important;
  padding-inline-end: 4.125rem !important;
}
html[dir=rtl] .is-invalid.form-select {
  background-position: left 0.75rem center, center left 2.25rem !important;
}
html[dir=ltr] .is-invalid.form-select {
  background-position: right 0.75rem center, center right 2.25rem !important;
}

/* -------------------------------------------------------------------------- */
/*                                 placeholder                                */
/* -------------------------------------------------------------------------- */
.form-control::placeholder {
  font-family: "IBM Plex Sans Arabic", sans-serif;
  font-size: 16;
}

.form-filled .form-control {
  padding: 0.8rem 1rem;
  background-color: #f3f4f6;
  border: none;
}
.form-filled .form-control::placeholder {
  color: #6c737f;
  font-size: medium;
}
.form-filled .input-group {
  overflow: hidden;
  background-color: #f3f4f6;
  border: none;
}
.form-filled .input-group .form-select {
  background-color: transparent;
}
.form-filled .input-group > * {
  border: none;
  border-radius: 0;
}
.form-filled .form-select {
  background-color: #f3f4f6;
  border: none;
  padding-block: 0.8rem !important;
}

