ol.item-list {
  list-style: none;
  counter-reset: item-counter;
  padding-left: 0;
}
ol.item-list li {
  background-color: #f9fafb;
  padding: 1.2rem;
  font-size: 18px;
  font-weight: 500;
  border-radius: 8px;
  counter-increment: item-counter;
  position: relative;
  padding-inline-start: 4.5rem;
  /* Adjust gap between items */
  min-height: 40px;
  /* Match circle height */
  display: flex;
  align-items: center;
}
ol.item-list li::before {
  content: counter(item-counter);
  position: absolute;
  width: 35px;
  height: 35px;
  background-color: #f3f4f6;
  /* Circle color - change as needed */
  color: #1b8354;
  /* Number color */
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  margin-inline-start: 1rem;
  right: 0;
}
html[dir=ltr] ol.item-list li::before {
  left: 0;
}

ul.dot-list {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}
ul.dot-list li {
  background-color: #f9fafb;
  padding: 1.2rem;
  font-size: 18px;
  font-weight: 500;
  border-radius: 8px;
  position: relative;
  padding-inline-start: 3rem;
  min-height: 40px;
  display: flex;
  align-items: center;
}
ul.dot-list li:last-child {
  margin-bottom: 0;
}
ul.dot-list li::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #1b8354;
  border-radius: 50%;
  inset-inline-start: 1.2rem;
  top: 50%;
  transform: translateY(-50%);
}

